import React from 'react'
import { Button, ButtonGroup } from 'react-bootstrap'
import { toast } from 'react-toastify';
import ModalAlert from './ModalAlert';

interface Props {
    Identificador: string,
    data: any
    setData: (d: any) => void
}
interface State {
    warning: boolean
    msnWarnig: string
    accion: () => void
}
export default class CacheStorage extends React.Component<Props, State> {
    borrarCache = false;
    state = { warning: false, msnWarnig: '', accion: () => { } };
    render() {
        let { warning, msnWarnig, accion } = this.state;
        return <ButtonGroup>
            <Button bsStyle="info" onClick={this.guardarCache.bind(this)} data-tip="GUARDAR EN CACHE">
                <span className="glyphicon glyphicon-save-file"></span>
            </Button>
            <ModalAlert open={warning} msn={msnWarnig} accion={accion} title='¬°PRECAUCION!' close={this.close.bind(this)} />
            <Button bsStyle="warning" onClick={this.cargarCache.bind(this)} data-tip="CARGAR DESDE CACHE">
                <span className="glyphicon glyphicon-open-file" />
            </Button>
        </ButtonGroup>
    }
    guardarCache() {
        let dataCache = localStorage.getItem('guardar' + this.props.Identificador);
        if (dataCache !== null)
            this.setState({
                accion: () => {
                    localStorage.setItem('guardar' + this.props.Identificador, JSON.stringify(this.props.data))
                    this.borrarCache = false;
                    toast("DATOS GUARDADOS", { type: toast.TYPE.SUCCESS })
                },
                warning: true,
                msnWarnig: '¬°YA HAY DATOS GUARDADOS EN LA CACHE! ¬øSEGURO QUE DESEA SOBREESCRIBIR CON LOS DATOS GUARDADOS CON LOS ACTUALES?'
            })
        else
            localStorage.setItem('guardar' + this.props.Identificador, JSON.stringify(this.props.data))

    }
    cargarCache() {
        let dataCache = localStorage.getItem('guardar' + this.props.Identificador);
        if (dataCache !== null)
            this.setState({
                accion: () => {
                    if (dataCache !== null)
                        this.props.setData(JSON.parse(dataCache))
                    this.borrarCache = true;
                    toast("DATOS CARGADOS", { type: toast.TYPE.SUCCESS })
                },
                warning: true,
                msnWarnig: '¬øSEGURO QUE DESEA SOBREESCRIBIR CON LOS DATOS ACTUALES CON LOS GUARDADOS?'
            })
        else
            toast("NO Hay DATOS GUARDADOS", { type: toast.TYPE.ERROR })
    }
    close() {
        this.setState({ warning: false });
    }
}