import React from 'react';
import Tabla from '../../Utils/Table';
import { GeneralFila } from '../../Modelos/General';
import { filaLabel } from '../../Utils/Utiles';
import FilaCotizacion from './FilaCotizacio'
import { Link } from 'react-router-dom';
export default class ListCotizaciones extends Tabla {
    placeholder = "BUSCAR COTIZACI√ìN";
    estatus = [{ value: 0, label: 'CANCELADO' }, { value: 1, label: 'PENDIENTE' }, { value: 2, label: 'PROCESADA' }];
    buscarLabel: filaLabel[] = ["codigo", "nombre", 'fecha', 'nota', 'telefono'];
    title() {
        return <>
            <div className="btn-group pull-right">
                <Link to="nueva_cotizacion" className="btn btn-info"><span className="glyphicon glyphicon-plus"></span> Nueva Cotizaci√≥n</Link>
            </div>
            <h4><i className="glyphicon glyphicon-search"></i> Buscar Cotizaciones</h4>
        </>
    }
    Row(p: GeneralFila) {
        return <FilaCotizacion update={this.set.bind(this)} key={"cot" + p.codigo + p.status} data={p} />
    }
    set() {
        this.update('/api/Cotizacion/lista');
    }
}
