import React from 'react'
import { Button, Modal, Row, Col, InputGroup, FormControl } from 'react-bootstrap'
import { toast } from 'react-toastify'
import Cliente, { ClienteNull } from '../../Modelos/Cliente'
import Proveedor from '../../Modelos/Proveedor'
import InputText from '../../Utils/InputText'
import SetText from '../../Utils/setTex'
import { numberFormat, post } from '../../Utils/Utiles'
interface Props {
    open: boolean
    cliente: Cliente | null
    type: 'Cliente' | 'Proveedor'
    title: string
    close: () => void
    add?: (c: Cliente) => void
}
export default class FormCliente extends React.Component<Props, Cliente> {
    constructor(p: Props) {
        super(p)
        this.setState = this.setState.bind(this);
        if (p.cliente !== null)
            this.state = p.cliente
        else
            this.state = ClienteNull;
    }

    render() {
        let { setState, state } = this
        return <Modal animation show={this.props.open} onHide={this.props.close}>
            <Modal.Header closeButton>
                <h4>{this.props.title}</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col xs={12}>
                        {this.inputCodeMode()}
                    </Col>
                    <Col xs={12}><br />
                        <InputText placeholder="AILANTHUS SISTEMS C.A." widthLabel={150} label="RASON SOCIAL" maxLength={150}
                            value={state.nombre} setData={setState} update="nombre" />
                    </Col>
                    <Col xs={12}><br />
                        <InputText placeholder="VENTAS@AILANTHUS-SISTEMS.COM" widthLabel={100} label="CORREO" maxLength={150}
                            value={state.correo} setData={setState} update="correo" />
                    </Col>
                    <Col xs={12}><br />
                        <InputText placeholder='(58) 251-2378406 (TELEFAX) / 4826705, (58) 414-5088735 / 416-6506831' maxLength={120}
                            widthLabel={100} label="TEL√âFONO" value={state.telefono} setData={setState} update="telefono" />
                    </Col>
                    <Col xs={12}><br />
                        <InputText placeholder='ARMANDO SULBARAN' widthLabel={200} label="PERSONA DE CONTACTO"
                            maxLength={50} value={state.contacto} setData={setState} update="contacto" />
                    </Col>
                    {(this.props.type === 'Cliente') ? <Col xs={12}><br />
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '250px', position: 'relative', backgroundColor: "#eee" }} className="form-control">TIPO DE CONTRIBUYENTE</span>
                            <select style={{ width: '150px', position: 'relative' }} onChange={this.setContribuyente.bind(this)}
                                className="form-control" defaultValue={state.tipo_contribuyente}>
                                <option value="ORDINARIO">ORDINARIO</option>
                                <option value="ESPECIAL">ESPECIAL</option>
                            </select>
                            {(state.tipo_contribuyente === 'ESPECIAL') ?
                                <SetText style={{ width: 'calc(100% - 400px)' }} value={state.retencion} setData={setState}
                                    update="retencion" type='number' step={1} /> :
                                <span style={{ width: 'calc(100% - 400px)', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }}
                                    className="form-control">{numberFormat(state.retencion)}</span>}
                        </InputGroup>
                    </Col> : <></>}
                    <Col xs={12}><br />
                        <b>DIRECCION</b>
                        <SetText placeholder='CARRERA 4, ESQUINA CALLE 18, ZONA INDUSTIAL I, GALPON MANPEG, PISO. 1 OFC.2 Y 3, BARQUISIMETO, ESTADO LARA.'
                            value={state.direccion} setData={setState} update="direccion" type='textarea' maxLength={300} />
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button className="pull-right" onClick={this.get.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Modal.Footer>
        </Modal>
    }
    isInvalid(s: string) {
        if (s === "")
            return true
        return false
    }
    inputCodeMode() {
        let { setState, state } = this
        if (this.props.cliente !== null)
            return <InputGroup className="mb-3">
                <span style={{ width: '5px', position: 'relative', backgroundColor: "#eee" }} className="form-control">RIF</span>
                <FormControl disabled defaultValue={state.codigo} />
            </InputGroup>
        return <InputText mask='A-99999999-9' placeholder="_-________-_" widthLabel={50} maxLength={20}
            label="RIF" value={state.codigo} setData={setState} update="codigo" />
    }
    get() {
        if (this.state.codigo === "") {
            toast('DEBE COLOCAR EL DOCUMENTO LEGAL DEL CLIENTE', { type: toast.TYPE.ERROR })
        } else if (this.state.nombre === "") {
            toast('DEBE COLOCAR EL RASON SOCIAL', { type: toast.TYPE.ERROR })
        } else if (this.state.codigo === "") {
            toast('DEBE COLOCAR UN CORREO', { type: toast.TYPE.ERROR })
        } else if (this.state.direccion === "") {
            toast('DEBE COLOCAR UNA DIRECCION', { type: toast.TYPE.ERROR })
        } else if (this.state.contacto === "") {
            toast('DEBE COLOCAR LA PERSONA DE CONTACTO', { type: toast.TYPE.ERROR })
        } else {
            if (this.props.cliente === null) {
                this.create()
            } else {
                this.update()
            }
        }
    }

    async update() {
        this.props.close()
    }

    async create() {
        post('api/' + this.props.type + '/nuevo',
            this.state, data => {
                if (this.props.add)
                    this.props.add(data)
                this.props.close()
            })
    }
    setContribuyente(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string;
        this.setState({ tipo_contribuyente: value });

    }

}