import React from 'react'
import { Button, Modal, Row, Col, InputGroup, FormControl } from 'react-bootstrap'
import InputText from '../../Utils/InputText'
import { Producto, ProductoNull } from '../../Modelos/Productos'
import { numberFormat, post } from '../../Utils/Utiles'
import SetText from '../../Utils/setTex'
import DepartamentoSelector from './Departamento/Selector'
import UnidadesSelector from './Unidad/Selector'
import TipoSelector from './Tipos/Selector'
import { toast } from 'react-toastify'
interface Props {
    open: boolean
    close: () => void
    add: (p: Producto) => void
}
interface State extends Producto {
    cateName?: string
}
export default class FormProductos extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        this.state = ProductoNull;
        this.setState = this.setState.bind(this)
    }
    setDepartamento(departamento: string) {
        this.setState({ departamento })
    }

    setUnidad(unidad: number) {
        this.setState({ unidad })
    }

    setTipo(tipo: number) {
        this.setState({ tipo })
    }
    render() {
        let { setState, state: { descripcion, precio1, precio2, precio3, costo, tipo, unidad, departamento }, props: { open, close } } = this;
        return <Modal bsSize="lg" animation show={open} onHide={close}>
            <Modal.Header closeButton>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVO PRODUCTO</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col xs={12}>
                        <DepartamentoSelector defaultValue={departamento} change={this.setDepartamento.bind(this)} />
                    </Col>
                    <Col xs={12}><br />
                        <b>DESCRIPCI√ìN</b>
                        <SetText type="textarea" rows={3} maxLength={300} value={descripcion} setData={setState} update="descripcion" />
                    </Col>
                    <Col xs={6}><br />
                        <UnidadesSelector defaultValue={unidad} change={this.setUnidad.bind(this)} />
                    </Col>
                    <Col xs={6}><br />
                        <TipoSelector defaultValue={tipo} change={this.setTipo.bind(this)} />
                    </Col>
                    <Col xs={12}><br />
                        <InputText minValue={1} step="0.01" type="number" label="COSTO" widthLabel={70} value={costo} setData={setState} update="costo" />
                    </Col>
                    <Col xs={4}><br />
                        <InputText minValue={1} step="1" after="%" widthAfter={40} type="number" label="UTILIDAD 1" widthLabel={120} value={precio1} setData={setState} update="precio1" />
                    </Col>
                    <Col xs={4}><br />
                        <InputText minValue={1} step="1" after="%" widthAfter={40} type="number" label="UTILIDAD 2" widthLabel={120} value={precio2} setData={setState} update="precio2" />
                    </Col>
                    <Col xs={4}><br />
                        <InputText minValue={1} step="1" after="%" widthAfter={40} type="number" label="UTILIDAD 3" widthLabel={120} value={precio3} setData={setState} update="precio3" />
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">PRECIO 1</span>
                            <FormControl style={{ width: 'calc(100% - 100px)' }} disabled value={numberFormat(this.getPrecio(precio1))} />
                        </InputGroup>
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">PRECIO 1</span>
                            <FormControl style={{ width: 'calc(100% - 100px)' }} disabled value={numberFormat(this.getPrecio(precio2))} />
                        </InputGroup>
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">PRECIO 1</span>
                            <FormControl style={{ width: 'calc(100% - 100px)' }} disabled value={numberFormat(this.getPrecio(precio3))} />
                        </InputGroup>
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={this.create.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Modal.Footer>
        </Modal>
    }
    getPrecio(price: number) {
        let utilidad = (price / 100) + 1
        return this.state.costo * utilidad
    }
    create() {
        let { descripcion } = this.state;
        if (descripcion === '') {
            toast('debe colocar la persona de contacto', { type: toast.TYPE.ERROR })
        }
        post('api/Producto/nuevo', this.state, data => {
            this.props.add(data);
            this.props.close();
        })
    }
}
