import React from 'react'
import { ButtonGroup, Button } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import InputText from '../../../Utils/InputText'
import SetText from '../../../Utils/setTex'
import { numberFormat } from '../../../Utils/Utiles'

interface Props {
    producto: Detalles,
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
interface State extends Detalles {
    open: boolean,
}
export default class FilaFactura extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto, open: false }
        this.setState = this.setState.bind(this)
    }
    render() {
        let { unidades, precio, descripcion, codigo, comentario, open, costo, precio1, precio2, precio3 } = this.state
        return <>
            <tr>
                <td>{codigo}</td>
                <td>{descripcion}</td>
                <td><SetText minValue={1} type="number" setData={this.setState} value={unidades} update="unidades" /></td>
                <td>
                    <SetText step='0.01' listLabel={costo + 'list'}
                        minValue={parseFloat((costo * (1 + precio1 / 100)).toFixed(2))} type="number"
                        setData={this.setState} value={precio} update="precio" />
                    <datalist id={costo + 'list'}>
                        <option>{(costo * (1 + precio1 / 100)).toFixed(2)}</option>
                        <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                        <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                    </datalist>
                </td>
                <td>
                    <span style={{ width: '100%', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }}
                        className="form-control">{numberFormat(precio * unidades)}</span>
                </td>
                <td style={{ textAlign: 'right' }}>
                    <ButtonGroup>
                        <Button bsStyle="info" onClick={() => { this.setState({ open: !this.state.open }) }} data-tip="DEJAR NOTA">
                            <span className="glyphicon glyphicon-edit"></span>
                        </Button>
                        <Button bsStyle="danger" onClick={() => { this.props.remove(this.state) }} data-tip="QUITAR PRODUCTO">
                            <i className="glyphicon glyphicon-ban-circle"></i>
                        </Button>
                    </ButtonGroup>
                </td>
            </tr>
            <tr style={{ display: open ? '' : 'none' }}>
                <td colSpan={7}>
                    <InputText widthLabel={150} label="NOTA INTERNA" setData={this.setState} value={comentario} update="comentario" />
                </td>
            </tr>
        </>
    }
    componentWillUpdate(p: Props, s: State) {
        if (s.precio !== this.state.precio || s.unidades !== this.state.unidades || s.comentario !== this.state.comentario)
            this.props.edit(s)
    }
}