
import React from 'react'
import { ButtonGroup, Button } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import SetText from '../../../Utils/setTex'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
}
interface State extends Detalles { }
export default class FilaEntrada extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto, precio: p.producto.costo, unidades: 1, comentario: '' };
        this.setState = this.setState.bind(this)
    }
    render() {
        let { unidades, precio, descripcion, codigo } = this.state
        return <>
            <tr>
                <td>{codigo}</td>
                <td>{descripcion}</td>
                <td><SetText minValue={1} step='0.01' type="number" setData={this.setState} value={unidades} update="unidades" /></td>
                <td><SetText minValue={1} step='0.01' type="number" setData={this.setState} value={precio} update="precio" /></td>
                <td>
                    <span style={{ width: '100%', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }}
                        className="form-control">{(precio * unidades).toFixed(2)}</span>
                </td>
                <td style={{ textAlign: 'right' }}>
                    <ButtonGroup>
                        <Button bsStyle="primary" onClick={() => { this.props.add(this.state) }} data-tip="QUITAR PRODUCTO">
                            <i className="glyphicon glyphicon-plus"></i>
                        </Button>
                    </ButtonGroup>
                </td>
            </tr>
        </>
    }


}