import { CondicionesData, GeneralData, getSubtotal, sentDetalles, getCosto } from "./General";

export interface Cotizacion extends CondicionesData {
    cod_cliente: string
    plantilla: number
    tasa: number
    dolar: boolean
}
export function sentCotizacion(e: Cotizacion, iva: number) {
    let subtotal = getSubtotal(e.detalles),
        impuesto = subtotal * (iva / 100),
        total = subtotal + impuesto;
    let { cod_cliente, validez, tiempo_entrega, forma_pago, nota, plantilla } = e;
    return {
        cod_cliente,
        plantilla,
        validez,
        tiempo_entrega,
        forma_pago,
        nota,
        total,
        impuesto,
        subtotal,
        detalles: sentDetalles(e.detalles)
    }
}
export interface Nota extends GeneralData {
    cod_cliente: string
}
export function sentNota(e: Nota, porc_impuesto: number) {
    let subtotal = getSubtotal(e.detalles),
        impuesto = subtotal * (porc_impuesto / 100),
        total = subtotal + impuesto;

    let { cod_cliente, nota } = e;
    return {
        cod_cliente,
        porc_impuesto,
        impuesto,
        subtotal,
        total,
        nota,
        detalles: sentDetalles(e.detalles)
    }
}
export interface Factura extends GeneralData {
    cod_cliente: string
    condicion: string
}
export function sentFactura(e: Factura, porc_impuesto: number) {
    let subtotal = getSubtotal(e.detalles),
        costo = getCosto(e.detalles),
        impuesto = subtotal * (porc_impuesto / 100),
        total = subtotal + impuesto;

    let { cod_cliente, nota, condicion, } = e;
    return {
        cod_cliente,
        condicion,
        porc_impuesto,
        costo,
        impuesto,
        subtotal,
        total,
        nota,
        detalles: sentDetalles(e.detalles)
    }
}