import React from 'react';
import { Detalles } from '../../../Modelos/General';
import { Producto, productoLabel } from '../../../Modelos/Productos';
import ModalBusqueda from '../../../Utils/ModalBusqueda';
import FilaEntrada from './FilaEntrada';
import { filtro } from '../../../Utils/Utiles';
import FilaSeleccion from './FilaSeleccion';
import FilaFactura from './FilaFactura';
import { Button, Row } from 'react-bootstrap';
import DepartamentoSelector from '../Departamento/Selector';
interface Props {
    type: 'Cotizacion' | 'Compra' | 'Factura' | 'Nota' | 'Orden' | 'Salida' | 'Entrada' | 'Precio' | 'Seleccion'
    add: (p: Detalles) => void
}
export default class BuscarProducto extends ModalBusqueda<Props, Producto, productoLabel>{
    buscarLabel: productoLabel[] = ['codigo', 'descripcion'];
    departamento: string = '';
    title() {
        return <h4>BUSCAR PRODUCTOS</h4>
    }
    setDepartamento(departamento: string) {
        this.departamento = departamento;
        this.forceUpdate();
    }
    renderBusqueda() {
        return <Row>
            <div className="col-md-12">
                <div style={{ position: 'relative', width: "100%" }} className="input-group">
                    <input style={{ position: 'relative', width: "50%" }} onKeyUp={this.setBuscar.bind(this)} name="buscar" placeholder={this.placeholder} className="form-control" />
                    <DepartamentoSelector width='300px' defaultValue={this.departamento} change={this.setDepartamento.bind(this)} />
                    <div className="input-group-append">
                        <Button style={{ width: '100px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="primary">
                            <span className="glyphicon glyphicon-search"></span> BUSCAR
                    </Button>
                    </div>
                </div>
            </div>
        </Row>
    }
    Header() {
        return <tr className="info">
            <th style={{ minWidth: "100px", maxWidth: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>CODIGO {this.flechas("codigo")}</th>
            <th style={{ backgroundColor: this.colorOrden("descripcion") }} onClick={() => { this.setOrden("descripcion") }}>DESCRIPCION {this.flechas("descripcion")}</th>
            {this.props.type !== 'Seleccion' ? <>
                <th style={{ width: "100px", textAlign: 'right' }}>CANTIDAD</th>
                <th style={{ width: "150px", textAlign: 'right' }}>PRE/UNT</th>
                <th style={{ width: "200px", textAlign: 'right' }}>PRECIO</th>
            </> : <></>}
            <th >ACCIONES</th>
        </tr>
    }
    Row(c: Producto) {
        switch (this.props.type) {
            case 'Seleccion':
                return <FilaSeleccion key={c.codigo + c.descripcion} producto={c} add={this.props.add} />
            case 'Entrada': case 'Compra': case 'Orden':
                return <FilaEntrada key={c.codigo + c.descripcion} producto={c} add={this.props.add} />
            case 'Nota': case 'Factura': case 'Cotizacion':
                return <FilaFactura key={c.codigo + c.descripcion} producto={c} add={this.props.add} />
        }
        return <></>;
    }
    filtros() {
        let { type } = this.props;
        let departamento = this.departamento;
        let itens = super.filtros();
        itens = filtro(itens, 1, 'estatus');
        if (departamento !== '')
            itens = itens.filter(c => {
                if (c.departamento === departamento) return true;
                return false;
            });
        if (type === 'Factura' || type === 'Nota')
            itens = itens.filter(c => {
                if (c.cantidad > 0) return true;
                return false;
            });
        this.length = itens.length;
        return itens;
    }
    set() {
        this.update('/api/Producto/lista');
    }
}