import React, { CSSProperties } from "react"
import { maskFormat, numberFormat } from "./Utiles"

interface Props {
    id?: string
    setData: (data: any) => void,
    update: string
    type?: "number" | "password" | "date" | 'textarea'
    minValue?: number
    maxLength?: number
    value: string | number
    step?: number | string
    style?: CSSProperties
    placeholder?: string
    listLabel?: string
    mask?: string
    rows?: number
}
interface State {
    selected: boolean
}

export default class SetText extends React.Component<Props, State> {
    ref = React.createRef<HTMLInputElement>()
    area = React.createRef<HTMLTextAreaElement>()
    constructor(p: Props) {
        super(p);
        this.state = { selected: false };
    }
    render() {
        let { type, id, style, placeholder, listLabel, maxLength, value, rows } = this.props
        if (type === 'textarea') {
            return <textarea className='form-control' ref={this.area}
                maxLength={maxLength} rows={rows ? rows : 5} id={id} style={style}
                placeholder={placeholder} defaultValue={value}
                onBlur={this.onBlur.bind(this)}
                onKeyUp={this.keyUp.bind(this)}
                onFocus={this.onFocus.bind(this)} />
        }
        return <>
            <input className='form-control' maxLength={maxLength}
                id={id} list={listLabel} placeholder={placeholder} ref={this.ref}
                defaultValue={this.getValue()}
                {...this.getProps()}
                onBlur={this.onBlur.bind(this)}
                onKeyUp={this.keyUp.bind(this)}
                onFocus={this.onFocus.bind(this)} />
        </>
    }
    stringValue() {
        let input = this.ref.current ? this.ref.current : false;
        if (input) {
            let { setData, update } = this.props;
            if (update !== undefined)
                setData({ [update]: input.value.toUpperCase() });
            input.value = input.value.toUpperCase();
        } else {
            let input = this.area.current ? this.area.current : false;
            if (input) {
                let { setData, update } = this.props;
                if (update !== undefined)
                    setData({ [update]: input.value.toUpperCase() });
                input.value = input.value.toUpperCase();
            }
        }
    }
    numberValue() {
        let input = this.ref.current;
        if (input) {
            let { minValue, setData, update } = this.props,
                r = (input.value === "") ? 0 : parseFloat(input.value);
            if (minValue)
                if (r < minValue)
                    r = minValue;
            if (update !== undefined)
                setData({ [update]: r });
        }
    }
    onBlur() {
        this.setState({ selected: false })
        let { type } = this.props
        if (type === "number") {
            this.numberValue();
        } else {
            this.stringValue();
        }
    }
    keyUp(e: React.KeyboardEvent<HTMLInputElement | HTMLTextAreaElement>) {
        let { update, setData, type, mask } = this.props
        let { value } = e.currentTarget
        if (type !== "number" && type !== 'password') {
            let r = value.toUpperCase()
            if (mask) {
                if (e.keyCode !== 8) {
                    r = maskFormat(r, mask);
                }
                if (update !== undefined)
                    setData({ [update]: r })
            }
        }
    }
    onFocus() {
        this.setState({ selected: true })
        let input = this.ref.current;
        if (input) {
            let { value } = this.props;
            input.value = value + '';
        }
    }
    getValue() {
        let { value, type } = this.props;
        if (type === 'number') {
            let r = value as number;
            return this.state.selected ? r : numberFormat(r);
        }
        let s = value as string
        return s.toUpperCase();
    }
    getProps() {
        let { type, step, style } = this.props
        if (type === 'number') {
            style = { ...style, textAlign: 'right' }
            if (this.state.selected) {
                return { step, minLength: 0, min: 0, type, style, defaultValue: this.getValue() };
            }
        }
        if (this.state.selected) {
            return { style, defaultValue: this.getValue() };
        }
        return { style, value: this.getValue() }
    }
}