import React from 'react';
import { GeneralFila } from '../../Modelos/General';
import FilaGeneraModal from '../../Utils/FilaGeneraModal';
import ModalBusqueda from '../../Utils/ModalBusqueda';
import { filaLabel } from '../../Utils/Utiles';
interface Props {
    add: (p: GeneralFila) => void
}
export default class BuscarCotizacion extends ModalBusqueda<Props, GeneralFila, filaLabel>{
    buscarLabel: filaLabel[] = ['codigo', 'nombre', 'fecha',];
    estatus = [{ value: 0, label: 'CANCELADO' }, { value: 1, label: 'PENDIENTE' }, { value: 2, label: 'PROCESADA' }];

    title() {
        return <h4>BUSCAR COTIZACION</h4>
    }
    Header() {
        return <tr className="info">
            <th style={{ width: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>NUMERO {this.flechas("codigo")}</th>
            <th style={{ width: "90px", backgroundColor: this.colorOrden("fecha") }} onClick={() => { this.setOrden("fecha") }}>FECHA {this.flechas("fecha")}</th>
            <th style={{ backgroundColor: this.colorOrden("nombre") }} onClick={() => { this.setOrden("nombre") }}>CLIENTE {this.flechas("nombre")}</th>
            <th style={{ width: "100px" }}>ESTATUS</th>
            <th style={{ width: "135px" }}>ACCIONES</th>
        </tr>
    }
    Row(c: GeneralFila) {
        return <FilaGeneraModal key={c.codigo+c.status} data={c} type='Cotizacion' add={this.add.bind(this)} />
    }
    add(c: GeneralFila) {
        this.props.add(c);
        this.props.close();
    }
    filtros() {
        let itens = super.filtros();
        this.length = itens.length;
        return itens;
    }
    set() {
        this.update('/api/Cotizacion/lista');
    }
}