import React from 'react';
import { Button, Row } from 'react-bootstrap';
import { GeneralFila } from '../../Modelos/General';
import FilaGeneraModal from '../../Utils/FilaGeneraModal';
import ModalBusqueda from '../../Utils/ModalBusqueda';
import { filaLabel } from '../../Utils/Utiles';
interface Props {
    add: (p: GeneralFila) => void
    cargarCache: () => void
}
export default class BuscarGuardado extends ModalBusqueda<Props, GeneralFila, filaLabel>{
    buscarLabel: filaLabel[] = ['codigo', 'nombre', 'fecha',];

    title() {
        return <h4>BUSCAR GUARDADOS</h4>
    }
    renderBusqueda() {
        return <Row>
            <div className="col-md-9">
                <div style={{ position: 'relative', width: "100%" }} className="input-group">
                    <input style={{ position: 'relative', width: "70%" }} onKeyUp={this.setBuscar.bind(this)} name="buscar" placeholder={this.placeholder} className="form-control" />
                    <div className="input-group-append">
                        <Button style={{ width: '100px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="primary">
                            <span className="glyphicon glyphicon-search"></span> BUSCAR
                    </Button>
                    </div>
                </div>
            </div>
            <Button style={{ width: '200px', }} onClick={this.props.cargarCache} bsStyle="warning">
                <span className="glyphicon glyphicon-open-file"></span> CARGAR DESDE CACHE
            </Button>
        </Row>
    }
    Header() {
        return <tr className="info">
            <th style={{ width: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>NUMERO {this.flechas("codigo")}</th>
            <th style={{ width: "90px", backgroundColor: this.colorOrden("fecha") }} onClick={() => { this.setOrden("fecha") }}>FECHA {this.flechas("fecha")}</th>
            <th style={{ backgroundColor: this.colorOrden("nombre") }} onClick={() => { this.setOrden("nombre") }}>USUARIO {this.flechas("nombre")}</th>
            <th style={{ width: "100px" }}>ESTATUS</th>
            <th style={{ width: "135px" }}>ACCIONES</th>
        </tr>
    }
    Row(c: GeneralFila) {
        return <FilaGeneraModal key={c.codigo + c.status} data={c} type='Cotizacion' add={this.add.bind(this)} />
    }
    add(c: GeneralFila) {
        this.props.add(c);
        this.props.close();
    }
    filtros() {
        let itens = super.filtros();
        this.length = itens.length;
        return itens;
    }
    set() {
        this.update('/api/Cotizacion/plantillas');
    }
}