
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import FilaProto from '../../Utils/FilaProto';
import RegistrarFactura from './RegistrarFactura';

export default class FilaCompra extends FilaProto {
    constructor(p: any) {
        super(p);
        this.Label = 'COMPRA';
        this.tipo = 'Compra';
    }
    getModal() {
        let { codigo } = this.props.data;
        return <>
            <RegistrarFactura
                id={codigo}
                close={this.close.bind(this)}
                open={this.state.aux}
            />
        </>
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="label label-danger">CANCELADA</span>
            case 1:
                return <span className="label label-warning">SIN FACTURA</span>
            case 2:
                return <span className="label label-success">FACTURADA</span>
        }
    }
    getButtons() {
        return <ButtonGroup className="pull-right">
            {(this.props.data.status === 1) ?
                <Button data-tip="REGISTAR FACTURA"
                    onClick={() => { this.setState({ aux: true }) }} >
                    <i className="glyphicon glyphicon-pencil"></i>
                </Button> : <></>}
            <Button data-tip="VER IMPRECION" bsStyle="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="glyphicon glyphicon-download"></i>
            </Button>
            {(this.props.data.status > 0) ?
                <Button data-tip="CANCELAR" bsStyle="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="glyphicon glyphicon-ban-circle"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
}