import React from 'react'
import { Departamento } from '../../../Modelos/Productos';
import { getPreCache } from '../../../Utils/Utiles';

interface Props {
    change: (r: string) => void
    defaultValue: string
    width?: string
}
interface State {
    Departamentos: Departamento[] | 'carro'
}
export default class DepartamentoSelector extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p);
        this.state = { Departamentos: 'carro' };
    }
    setUsers(c: Departamento[]) {
        this.setState({ Departamentos: c })
    }
    render() {
        return <select style={{ width: this.props.width }} onChange={this.setRol.bind(this)} className="form-control" defaultValue={this.props.defaultValue}>
            <option value={''}>SELECCIONAR UN DEPARTAMENTO</option>
            {(this.state.Departamentos !== 'carro') ? this.state.Departamentos.map(c => {
                return <option key={"user" + c.codigo} value={c.codigo}>{c.descripcion}</option>
            }) : <></>}
        </select>
    }
    componentDidMount() {
        this.set();
    }
    setRol(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string
        this.props.change(value)
    }
    set() {
        getPreCache('/api/Producto/departamentos', data => {
            this.setUsers(data);
        });
    }
}