import React from 'react'
import { Tipo } from '../../../Modelos/Productos';
import { getPreCache } from '../../../Utils/Utiles';

interface Props {
    change: (r: number) => void
    defaultValue: number

}
interface State {
    tipos: Tipo[] | 'carro'
}
export default class TipoSelector extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p);
        this.state = { tipos: 'carro' };
    }
    setUsers(c: Tipo[]) {
        this.setState({ tipos: c })
    }
    render() {
        return <select onChange={this.setRol.bind(this)} className="form-control" defaultValue={this.props.defaultValue}>
            <option value={-1}>SELECCIONAR TIPO DE PRODUCTO</option>
            {(this.state.tipos !== 'carro') ? this.state.tipos.map(c => {
                return <option key={"user" + c.codigo} value={c.codigo}>{c.descripcion}</option>
            }) : <></>}
        </select>
    }
    componentDidMount() {
        this.set();
    }
    setRol(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string
        let r = parseInt(value)
        this.props.change(r)
    }
    set() {
        getPreCache('/api/Producto/tipo', data => {
            this.setUsers(data);
        });
    }
}