import React from "react";
import { Button, Col, Panel, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { Compra, compraNull, sentCompra } from "../../Modelos/Compra";
import { GeneralFila } from "../../Modelos/General";
import CacheStorage from "../../Utils/CacheStorage";
import InputText from "../../Utils/InputText";
import SetText from "../../Utils/setTex";
import { getPreCache, post } from "../../Utils/Utiles";
import ClienteSelector from "../Clientes/ClienteSelector";
import BuscarOrden from "../Orden/BuscarOrden";
import AuxiliarCompra from "./Nuevo/AuxiliarCompra";
interface State extends Compra {
    open: boolean
    cargar: boolean
}
export default class NuevaCompra extends React.Component<any, State>{
    impuesto = 0;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...compraNull, open: false, cargar: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Compra/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <BuscarOrden open={state.cargar} add={this.cargar.bind(this)} close={() => { this.setState({ cargar: false }) }} />
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVA COMPRA</h4>
            </Panel.Heading>
            <Panel.Body>
                <ClienteSelector type='Proveedor' Cliente={state.cod_proveedor} add={this.setProveedor.bind(this)} />
                <Button style={{ marginBottom: '15px' }}
                    onClick={() => { this.setState({ open: !this.state.open }) }}
                    bsStyle='primary'>DATOS OPCIONALES</Button>
                <Panel.Collapse>
                    <Panel bsStyle="info">
                        <Panel.Body>
                            <Row>
                                <Col data-tip="NUMERO DE FACTURA" xs={3}>
                                    <InputText maxLength={10} widthLabel={70} label="N¬∞ F" value={state.cod_documento} setData={this.setState} update="cod_documento" />
                                </Col>
                                <Col data-tip="NUMERO DE CONTROL" xs={3}>
                                    <InputText maxLength={10} widthLabel={70} label="N¬∞ C" value={state.nun_control} setData={this.setState} update="nun_control" />
                                </Col>
                                <Col data-tip="FECHA DE LA FACTURA" xs={3}>
                                    <InputText type='date' widthLabel={80} label="FECHA" value={state.fecha_documento} setData={this.setState} update="fecha_documento" />
                                </Col>
                                <Col data-tip="NUMERO DE NOTA DE ENTREGA" xs={3}>
                                    <InputText maxLength={10} widthLabel={80} label="N¬∞ N/E" value={state.cod_nota} setData={this.setState} update="cod_nota" />
                                </Col>
                                <Col xs={12}><br />
                                    <b>NOTA</b>
                                    <SetText type='textarea' rows={4} maxLength={600} value={state.nota} setData={this.setState} update="nota" />
                                </Col>
                            </Row>
                        </Panel.Body>
                    </Panel>
                </Panel.Collapse>
                <AuxiliarCompra Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} >
                    <Col style={{ width: "70px" }} xs={2}>
                        <Button onClick={() => { this.setState({ cargar: true }) }}
                            data-class="toolbody" data-html="true"
                            data-tip="COPIAR ORDEN <br> DE COMPRA" bsStyle="primary">
                            <span className="glyphicon glyphicon-folder-open" />
                        </Button>
                    </Col>
                    <CacheStorage data={this.props.data} setData={this.props.setData} Identificador='NuevaCompra' />
                </AuxiliarCompra>
            </Panel.Body>
        </Panel>
    }
    cargar(d: GeneralFila) {
        getPreCache('api/Orden/detalles/' + d.codigo, data => {
            data.id_orden = data.codigo;
            data.codigo = 0;
            this.setState(data)
        })
    }
    componentDidMount() {
        getPreCache('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
    }
    setProveedor(p: string) {
        this.setState({ cod_proveedor: p });
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_proveedor === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (procesar)
            this.state.detalles.forEach(p => {
                if (p.unidades > p.cantidad) {
                    toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                    procesar = false;
                } else if (p.costo > p.precio) {
                    toast(p.descripcion + ' ESTA POR DEBAJO DE SU COSTO REGISTRADO', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            })
        if (procesar)
            post('api/Compra/nuevo', sentCompra(this.state, this.impuesto), data => {
                this.setState({ codigo: data })
            })
    }
}