import React from 'react';
import Tabla from '../../Utils/Table';
import { GeneralFila } from '../../Modelos/General';
import { filaLabel } from '../../Utils/Utiles';
import FilaCompra from './FilaCompra';
import { Link } from 'react-router-dom';
export default class ListConpra  extends Tabla {
    placeholder = "BUSCAR COMPRA";
    estatus = [{ value: 0, label: 'CANCELADO' }, { value: 1, label: 'SIN FACTURA' }, { value: 2, label: 'FACTURADA' }];
    buscarLabel: filaLabel[] = ["codigo", "nombre", 'fecha', 'nota', 'telefono'];
    title() {
        return <>
            <div className="btn-group pull-right">
                <Link to="nueva_compra" className="btn btn-info"><span className="glyphicon glyphicon-plus"></span> NUEVA COMPRA</Link>
            </div>
            <h4><i className="glyphicon glyphicon-search"></i> BUSCAR COMPRA</h4>
        </>
    }
    Header() {
        let width = window.innerWidth - 890;
        return <tr className="info">
            <th style={{ width: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>NUMERO {this.flechas("codigo")}</th>
            <th style={{ width: "90px", backgroundColor: this.colorOrden("fecha") }} onClick={() => { this.setOrden("fecha") }}>FECHA {this.flechas("fecha")}</th>
            <th style={{ width: width + 'px', backgroundColor: this.colorOrden("nombre") }} onClick={() => { this.setOrden("nombre") }}>PROVEEDOR {this.flechas("nombre")}</th>
            <th style={{ width: "160px", backgroundColor: this.colorOrden("monto") }} onClick={() => { this.setOrden("monto") }}>MONTO {this.flechas("monto")}</th>
            <th style={{ width: "100px" }}>ESTATUS</th>
            <th style={{ width: "135px" }}>ACCIONES</th>
        </tr>
    }
    Row(p: GeneralFila) {
        return <FilaCompra update={this.set.bind(this)} key={"cot" + p.codigo + p.status} data={p}  />
    }
    set() {
        this.update('/api/Compra/lista');
    }
}
