import React from 'react'
import { ButtonGroup, Button } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import SetText from '../../../Utils/setTex'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
}
export default class FilaFactura extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        let { costo, precio1 } = p.producto;
        this.state = {
            ...p.producto,
            unidades: 1,
            comentario: '',
            precio: (costo * (1 + precio1 / 100))
        }
        this.setState = this.setState.bind(this);
    }
    render() {
        let { unidades, precio, descripcion, codigo, costo, precio1, precio2, precio3 } = this.state
        return <tr>
            <td>{codigo}</td>
            <td>{descripcion}</td>
            <td><SetText minValue={1} type="number" setData={this.setState} value={unidades} update="unidades" /></td>
            <td>
                <SetText step='0.01' listLabel={costo + 'list'}
                    minValue={costo * (1 + precio1 / 100)} type="number"
                    setData={this.setState} value={parseFloat(precio.toFixed(2))} update="precio" />
                <datalist id={costo + 'list'}>
                    <option>{(costo * (1 + precio1 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                </datalist>
            </td>
            <td>
                <span style={{ width: '100%', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }}
                    className="form-control">{(precio * unidades).toFixed(2)}</span>
            </td>
            <td style={{ textAlign: 'right' }}>
                <ButtonGroup>
                    <Button bsStyle="primary" onClick={() => { this.props.add(this.state) }} data-tip="QUITAR PRODUCTO">
                        <i className="glyphicon glyphicon-plus"></i>
                    </Button>
                </ButtonGroup>
            </td>
        </tr>
    }
}