import React from 'react'
import { Row, Col, Button, InputGroup, ButtonGroup, Panel } from 'react-bootstrap'
import FormCliente from './FormCliente'
import Cliente, { ClienteNull } from '../../Modelos/Cliente'
import { busqueda, getPreCache } from '../../Utils/Utiles'
interface Props {
    type: 'Cliente' | 'Proveedor'
    Cliente: string
    add: (Cliente: string) => void
}
interface State {
    lista: Cliente[]
    cliente: Cliente
    buscarNumbre: string
    buscarCodigo: string
    nuevoCliente: boolean
    open: boolean
}
export default class ClienteSelector extends React.Component<Props, State> {
    nombre = React.createRef<HTMLInputElement>();
    codigo = React.createRef<HTMLInputElement>();
    constructor(p: Props) {
        super(p)
        this.state = { nuevoCliente: false,  open: false, lista: [], cliente: ClienteNull, buscarCodigo: '', buscarNumbre: '' }
    }
    render() {
        const { state: { nuevoCliente,  lista, open, cliente, buscarCodigo, buscarNumbre }, props: { type } } = this
        return <Panel bsStyle="info" id="tabla" expanded={open}>
            <FormCliente type={type} cliente={null} title={"NUEVO " + this.getLabel()} open={nuevoCliente} add={this.seleccion.bind(this)} close={this.close.bind(this)} />
            <Panel.Body>
                <Row><Col xs={12}><h4><b>DATOS DEL {this.getLabel()}</b></h4></Col></Row>
                <Row>
                    <Col style={{ width: '30%' }} xs={4}>
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '50px', position: 'relative', backgroundColor: "#eee" }} className="form-control">RIF</span>
                            <input style={{ width: 'calc(100% - 60px)', position: 'relative' }} list="inputCodigo" className="form-control"
                                ref={this.codigo}
                                onKeyUp={this.limpiarCodigo.bind(this)}
                                onChange={this.setBuscarCodigo.bind(this)}
                                defaultValue={buscarCodigo} />
                        </InputGroup>
                        <datalist id="inputCodigo">
                            {this.getList(busqueda(lista, buscarCodigo, ['codigo']), 'codigo')}
                        </datalist>
                    </Col>
                    <Col style={{ width: 'calc(70% - 120px)' }} xs={6}>
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }} className="form-control">NOMBRE</span>
                            <input style={{ width: 'calc(100% - 100px)' }} list="inputNombre" className="form-control"
                                ref={this.nombre}
                                onKeyUp={this.limpiarNombre.bind(this)}
                                onChange={this.setBuscarNombre.bind(this)}
                                defaultValue={buscarNumbre} />
                        </InputGroup>
                        <datalist id="inputNombre">
                            {this.getList(busqueda(lista, buscarCodigo, ['nombre']), 'nombre')}
                        </datalist>
                    </Col>
                    <Col style={{ width: "110px" }} xs={2}>
                        <ButtonGroup className="float-right">
                            <Button data-tip="VER MAS" onClick={() => { this.setState({ open: !open }) }}>
                                <i className="glyphicon glyphicon-eye-open"></i>
                            </Button>
                            <Button data-tip={"NUEVO " + this.getLabel()} onClick={() => { this.setState({ nuevoCliente: true }) }} bsStyle="info">
                                <i className="glyphicon glyphicon-edit"></i>
                            </Button>
                        </ButtonGroup>
                    </Col>
                </Row>
                <Panel.Collapse>
                    <br />
                    <Row>
                        <Col xs={4}>
                            <InputGroup style={{ width: '100%' }} className="mb-3">
                                <span style={{ width: '140px', position: 'relative', backgroundColor: "#eee" }} className="form-control">CORREO</span>
                                <span style={{ width: 'calc(100% - 150px)', position: 'relative', backgroundColor: "#eee" }} className="form-control">{cliente.correo}</span>
                            </InputGroup>
                        </Col>
                        <Col xs={3}>
                            <InputGroup style={{ width: '100%' }} className="mb-3">
                                <span style={{ width: '140px', position: 'relative', backgroundColor: "#eee" }} className="form-control">TELEFONO</span>
                                <span style={{ width: 'calc(100% - 150px)', position: 'relative', backgroundColor: "#eee" }} className="form-control">{cliente.telefono}</span>
                            </InputGroup>
                        </Col>
                        <Col xs={5}>
                            <InputGroup style={{ width: '100%' }} className="mb-3">
                                <span style={{ width: '240px', position: 'relative', backgroundColor: "#eee" }} className="form-control">PERSONA DE CONTACTO</span>
                                <span style={{ width: 'calc(100% - 250px)', position: 'relative', backgroundColor: "#eee" }} className="form-control">{cliente.contacto}</span>
                            </InputGroup>
                        </Col>
                        <Col xs={12}>
                            <br />
                            <InputGroup style={{ width: '100%' }} className="mb-3">
                                <span style={{ width: '140px', position: 'relative', backgroundColor: "#eee" }} className="form-control">DICECCION</span>
                                <span style={{ width: 'calc(100% - 150px)', position: 'relative', backgroundColor: "#eee" }} className="form-control">{cliente.direccion}</span>
                            </InputGroup>
                        </Col>
                    </Row>
                </Panel.Collapse>
            </Panel.Body>
        </Panel>
    }
    seleccion(c: Cliente) {
        this.set();
        this.props.add(c.codigo);
        if (this.nombre.current)
            this.nombre.current.value = c.nombre;
        if (this.codigo.current)
            this.codigo.current.value = c.codigo;
        this.setState({ cliente: c, buscarCodigo: c.codigo, buscarNumbre: c.nombre });
    }
    setBuscarNombre(e: React.ChangeEvent<HTMLInputElement>) {
        let b = e.target.value.toUpperCase()
        this.setState({ buscarNumbre: b });
        this.state.lista.forEach((c) => {
            if (c.nombre === b)
                this.props.add(c.codigo);
        });
    }
    setBuscarCodigo(e: React.ChangeEvent<HTMLInputElement>) {
        let b = e.target.value.toUpperCase()
        this.setState({ buscarCodigo: b });
        this.state.lista.forEach((c) => {
            if (c.codigo === b)
                this.props.add(c.codigo);
        });
    }
    limpiarNombre(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.keyCode === 8) {
            e.currentTarget.value = '';
            this.limpiar();
        }
    }
    limpiarCodigo(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.keyCode === 8) {
            e.currentTarget.value = '';
            this.limpiar();
        }
    }
    limpiar() {
        this.setState({ cliente: ClienteNull, buscarCodigo: '', buscarNumbre: '' })
        if (this.nombre.current)
            this.nombre.current.value = "";
        if (this.codigo.current)
            this.codigo.current.value = "";
    }
    getList(lista: Cliente[], label: 'codigo' | 'nombre') {
        return lista.map(c => {
            return <option key={label + c.codigo}>{c[label]}</option>
        })
    }
    close() {
        this.setState({
            nuevoCliente: false
        })
    }
    componentWillReceiveProps(p: Props) {
        if (this.state.cliente.codigo !== p.Cliente)
            this.state.lista.forEach((c) => {
                if (c.codigo === p.Cliente) {
                    if (this.nombre.current)
                        this.nombre.current.value = c.nombre;
                    if (this.codigo.current)
                        this.codigo.current.value = c.codigo;
                    this.setState({ cliente: c, buscarCodigo: c.codigo, buscarNumbre: c.nombre });
                }
            });
    }
    componentDidMount() {
        this.actualizar();
    }
    actualizar() {
        this.set();
        setTimeout(() => {
            this.actualizar()
        }, 36000);
    }
    set() {
        getPreCache('/api/' + this.props.type + '/lista', data => {
            // data = filtro(data, 1, 'status');
            this.setState({ lista: data });
        });
    }
    getLabel() {
        if (this.props.type === 'Cliente')
            return 'CLIENTE'
        return 'PROVEEDOR'
    }
}