import React from 'react'
import { Unidad } from '../../../Modelos/Productos';
import { getPreCache } from '../../../Utils/Utiles';

interface Props {
    change: (r: number) => void
    defaultValue: number

}
interface State {
    Unidades: Unidad[] | 'carro'
}
export default class UnidadesSelector extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p);
        this.state = { Unidades: 'carro' };
    }
    setUsers(c: Unidad[]) {
        this.setState({ Unidades: c })
    }
    render() {
        return <select onChange={this.setRol.bind(this)} className="form-control" defaultValue={this.props.defaultValue}>
            <option value={-1}>SELECCIONAR UNIDAD DE MEDIDA</option>
            {(this.state.Unidades !== 'carro') ? this.state.Unidades.map(c => {
                return <option key={"user" + c.codigo} value={c.codigo}>{c.descripcion}</option>
            }) : <></>}
        </select>
    }
    componentDidMount() {
        this.set();
    }
    setRol(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string
        let r = parseInt(value)
        this.props.change(r)
    }
    set() {
        getPreCache('/api/Producto/unidad', data => {
            this.setUsers(data);
        });
    }
}