
import React from 'react';
import FilaProto from '../../Utils/FilaProto';

export default class FilaNota extends FilaProto {
    constructor(p: any) {
        super(p);
        this.Label = 'NOTA DE ENTREGA';
        this.tipo = 'Nota';
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="label label-danger">CANCELADA</span>
            case 1:
                return <span className="label label-warning">ENTREGADO</span>
            case 2:
                return <span className="label label-success">FACTURADA</span>
        }
    }
}