import React from "react"
import { getPreCache, numberFormat } from "../../../Utils/Utiles";

interface Props {
    imponible: number
    exento: number
}

export default class CalcularImpuesto extends React.Component<Props> {
    impuesto = 0;
    componentDidMount() {
        getPreCache('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
    }
    render() {
        let { imponible, exento } = this.props
        return <>
            <tr>
                <th colSpan={3} rowSpan={8}></th>
                <th style={{ textAlign: "right" }} colSpan={2}>SUBTOTAL:</th>
                <th style={{ textAlign: "right" }}>{numberFormat(imponible + exento)}</th>
            </tr>
            {exento > 0 ? <>
                <tr>
                    <th style={{ textAlign: "right" }} colSpan={2}>EXENTO:</th>
                    <th style={{ textAlign: "right" }}>{numberFormat(exento)}</th>
                </tr>
                <tr>
                    <th style={{ textAlign: "right" }} colSpan={2}>BASE IMPONIBLE:</th>
                    <th style={{ textAlign: "right" }}>{numberFormat(imponible)}</th>
                </tr>
            </> : <></>}
            <tr>
                <th style={{ textAlign: "right" }} colSpan={2}>IMPUESTO {this.impuesto}%:</th>
                <th style={{ textAlign: "right" }}>{this.calImpuesto()}</th>
            </tr>
            <tr>
                <th style={{ textAlign: "right" }} colSpan={2}>TOTAL:</th>
                <th style={{ textAlign: "right" }}>{this.calPagar()}</th>
            </tr>
        </>
    }
    calImpuesto() {
        let { imponible } = this.props;
        let impuesto = (this.impuesto / 100);
        return numberFormat(imponible * impuesto);
    }
    calPagar() {
        let { imponible, exento } = this.props;
        let impuesto = this.impuesto / 100;
        let total = imponible + exento + (imponible * impuesto);
        return numberFormat(total)
    }
}
