
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import { GeneralFila, tipoData } from '../Modelos/General';
import ModalPrint from './ModalPrint';
import ModalAlert from './ModalAlert';
import ModalDetalle from './ModalDetalle';
import { formatoFecha, numberFormat, zfill } from './Utiles';

interface Props {
    data: GeneralFila
    update?: () => void
}
interface State {
    print: boolean
    detalles: boolean
    aux: boolean
    eliminar: boolean
}
export default class FilaProto extends React.Component<Props, State>{
    Label: 'COTIZACI√ìN' | 'COMPRA' | 'FACTURA' | 'NOTA DE ENTREGA' | 'ORDEN DE COMPRA' = 'COTIZACI√ìN';
    tipo: tipoData = 'Nota';
    constructor(p: any) {
        super(p);
        this.state = { print: false, detalles: false, aux: false, eliminar: false };
    }
    render() {
        let cot = this.props.data,
            maxWidth = window.innerWidth - 890;
        return <tr >
            <th style={{ minWidth: "100px", maxWidth: "100px" }}>
                {this.tipo !== 'Cotizacion' ? <ModalDetalle
                    tipo={this.tipo} titulo={"DETALLES DE LA " + this.Label + " N¬∞ " + zfill(cot.codigo)}
                    id={cot.codigo} open={this.state.detalles} close={this.close.bind(this)}
                /> : <></>}
                <Button data-tip="VER DETALLEA" onClick={() => { this.setState({ detalles: true }) }} bsStyle="link" >
                    {zfill(cot.codigo)}
                </Button>
            </th>
            <th style={{ minWidth: "90px", maxWidth: "90px", paddingTop: 10 }}>{formatoFecha(cot.fecha)}</th>
            <th>
                <ReactTooltip />
                <Button
                    style={{ maxWidth, whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}
                    data-class="toolbody" data-html="true" data-tip={this.tooltipCliente()} bsStyle="link" >
                    {cot.nombre}
                </Button>
            </th>
            <th style={{ minWidth: "160px", maxWidth: "160px", paddingTop: 10, textAlign: 'right' }}>{numberFormat(cot.monto)}</th>
            <th style={{ minWidth: "100px", maxWidth: "100px", paddingTop: 5, fontSize: 17 }}>{this.getStatus()}</th>
            <th style={{ minWidth: "135px", maxWidth: "135px" }}>
                <ModalPrint title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(cot.codigo)}
                    url={"/api/" + this.tipo + "/PDF/" + cot.codigo}
                    open={this.state.print} close={this.close.bind(this)} />
                <ModalAlert title={"ELIMINAR " + this.Label + " N¬∞ " + zfill(cot.codigo)}
                    msn={"ESTA SEGURO QUE ELIMINARA LA " + this.Label + " N¬∞" + cot.codigo + "?"}
                    open={this.state.eliminar} close={this.close.bind(this)} accion={this.cancelar.bind(this)} />
                {this.getModal()}
                {this.getButtons()}
            </th>
        </tr >
    }
    getModal() {
        return <></>;
    }
    getStatus() {
        return <span className="label label-danger">CANCELADA</span>
    }
    getButtons() {
        return <ButtonGroup className="pull-right">
            <Button data-tip="VER IMPRECION" bsStyle="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="glyphicon glyphicon-download"></i>
            </Button>
            {(this.props.data.status === 1) ?
                <Button data-tip="CANCELAR" bsStyle="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="glyphicon glyphicon-ban-circle"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
    tooltipCliente() {
        let cot = this.props.data
        return "<h9 style='color: #5bc0de'>DATOS DE CONTACTO</h9><br />" +
            "<b>PERSONA DE CONTACTO:</b><br />" +
            cot.contacto + "<br />" +
            "<b>CORREO ELECTRONICO:</b><br />" +
            cot.correo + "<br />" +
            "<b>TELEFONO:</b><br />" +
            cot.telefono
    }
    close() {
        this.setState({ print: false, detalles: false, aux: false, eliminar: false });
    }
    cancelar() {
        fetch("/api/" + this.tipo + "/cancelar/" + this.props.data.codigo)
            .then(response => response.json())
            .then(data => {
                if (data === 1)
                    if (this.props.update)
                        this.props.update()
                this.close();
            });
    }
}