import React from 'react'
import { Row, Col, Button, Table, ButtonGroup } from 'react-bootstrap'
import { toast } from 'react-toastify'
import { removeElement, addUpdateArray } from '../../../Utils/ArrayFun'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import BuscarProducto from '../../Productos/BuscarProducto'
import ReactTooltip from 'react-tooltip'
import FilaProducto from './FilaProducto'
import CalcularImpuesto from './CalcularImpuesto'
import { Cotizacion } from '../../../Modelos/Factura'
import FormProductos from '../../Productos/FormProductos'
import FilaDolar from './FilaDolar'
interface State {
    buscarProducto: boolean
    nuevoProducto: boolean
}
interface Props {
    data: Cotizacion
    setData: (data: any) => void
    Prosesar?: () => void
}
export default class AuxiliarCotizacion extends React.Component<Props, State> {
    setData: (data: any) => void;
    constructor(p: Props) {
        super(p);
        this.setData = p.setData;
        this.state = { buscarProducto: false, nuevoProducto: false };
    }
    addProductos(producto: Detalles) {
        let cant = this.props.data.detalles.length;
        producto.precio = parseFloat(producto.precio.toFixed(2));
        let aux = {
            a: this.props.data.detalles, b: producto,
            isCompare: (a: Detalles, b: Detalles) => { if (a.codigo === b.codigo) return true; return false }
        }
        let detalles = addUpdateArray(aux);
        if (cant < detalles.length)
            toast('PRODUCTO ' + producto.descripcion + " AGREGADO", { type: toast.TYPE.SUCCESS })
        this.setData({ detalles })
    }
    removeProductos(producto: Detalles) {
        let aux = { a: this.props.data.detalles, b: producto, isCompare: (a: Detalles, b: Detalles) => { if (a.codigo === b.codigo) return true; return false } }
        this.setData({ detalles: removeElement(aux) })
        toast('PRODUCTO ' + producto.descripcion + " QUITADO", { type: toast.TYPE.SUCCESS })
    }
    createProducto(p: Producto) {
        this.addProductos({ ...p, unidades: 1, precio: p.costo * (1 + p.precio2 / 100), comentario: "" });
    }
    render() {
        let imp = 0;
        let ext = 0;
        let { buscarProducto, nuevoProducto } = this.state;
        let { data: { dolar, tasa }, setData } = this.props;
        return <>
            <FormProductos open={nuevoProducto} close={this.close.bind(this)} add={this.createProducto.bind(this)} />
            <BuscarProducto type='Cotizacion' open={buscarProducto} add={this.addProductos.bind(this)} close={this.close.bind(this)} />
            <ReactTooltip />
            <Row>
                <Col xs={6}>
                    {this.props.children}
                </Col>
                <Col xs={6}>
                    <Button data-tip="PROCESAR" style={{ marginRight: "15px" }} bsStyle="success"
                        className="pull-right" onClick={this.props.Prosesar} >
                        <span className="glyphicon glyphicon-print" />
                    </Button>
                    <ButtonGroup className="pull-right" style={{ marginRight: "15px" }}>
                        <Button data-tip="NUEVO PRODUCTO" onClick={() => { this.setState({ nuevoProducto: true }) }} bsStyle="info">
                            <i className="glyphicon glyphicon-edit"></i>
                        </Button>
                        <Button data-tip="BUSCAR PRODUCTO" onClick={() => { this.setState({ buscarProducto: true }) }} bsStyle="primary">
                            <span className="glyphicon glyphicon-search"></span>
                        </Button>
                    </ButtonGroup>
                    {dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }} bsStyle="warning"
                        className="pull-right" onClick={() => { setData({ dolar: !dolar }) }} >
                        <span className="glyphicon glyphicon-usd" />
                    </Button> : <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }} bsStyle="primary"
                        className="pull-right" onClick={() => { setData({ dolar: !dolar }) }} >
                        <b style={{ fontSize: '10px' }}>BS</b>
                    </Button>}
                </Col>
            </Row>
            <hr />
            <Table className="accordion" striped hover>
                <thead>
                    <tr className='info'>
                        <th style={{ width: "100px" }}>CODIGO</th>
                        <th>DESCRIPCI√ìN</th>
                        <th style={{ width: "100px", textAlign: 'right' }}>CANTIDAD</th>
                        <th style={{ width: "150px", textAlign: 'right' }}>PRE/UNT</th>
                        <th style={{ width: "200px", textAlign: 'right' }}>PRECIO</th>
                        <th style={{ width: "110px", textAlign: 'right' }}>ACCIONES</th>
                    </tr>
                </thead>
                <tbody>
                    {this.props.data.detalles.map(producto => {
                        let impo = 0, exen = 0;
                        if (producto.exento) {
                            exen += producto.precio * producto.unidades;
                        } else {
                            impo += producto.precio * producto.unidades;
                        }
                        if (dolar) {
                            exen = exen / tasa;
                            impo = impo / tasa;
                        }
                        ext += exen;
                        imp += impo;
                        return dolar ? <FilaDolar
                            key={'USD' + producto.codigo}
                            tasa={tasa}
                            producto={producto}
                            edit={this.addProductos.bind(this)}
                            remove={this.removeProductos.bind(this)} /> :
                            <FilaProducto
                                key={'BS' + producto.codigo}
                                producto={producto}
                                edit={this.addProductos.bind(this)}
                                remove={this.removeProductos.bind(this)} />

                    })}
                    <CalcularImpuesto exento={ext} imponible={imp} />
                </tbody>
            </Table>
        </>
    }
    close() {
        this.setState({ buscarProducto: false, nuevoProducto: false })
    }
}