import React from "react"
import { CSSProperties } from "react";
import { InputGroup } from "react-bootstrap"
import { maskFormat, numberFormat } from './Utiles';
interface Props {
    setData: (data: any) => void,
    label?: string
    widthLabel?: number
    widthAfter?: number
    update: string
    type?: "number" | "password" | 'date'
    value: string | number
    maxLength?: number
    minValue?: number
    valid?: boolean
    validMsn?: string
    step?: string
    after?: string
    mask?: string
    placeholder?: string
}
interface State {
    selected: boolean
}
export default class InputText extends React.Component<Props, State> {
    ref = React.createRef<HTMLInputElement>()
    constructor(p: Props) {
        super(p);
        this.state = { selected: false };
    }
    render() {
        let { widthLabel, widthAfter, maxLength, placeholder, type } = this.props
        let style: CSSProperties = { textAlign: (type === 'number') ? 'right' : undefined }
        let l = 0
        if (widthLabel) l += widthLabel;
        if (widthAfter) l += widthAfter;
        style = { width: 'calc(100% - ' + l + 'px)', ...style }
        return <InputGroup style={{ width: '100%' }} className="mb-3">
            {this.label()}
            <input className='form-control' maxLength={maxLength} placeholder={placeholder}
                defaultValue={this.getValue()} ref={this.ref} style={style}
                {...this.getProps()}
                onBlur={this.onBlur.bind(this)}
                onKeyUp={this.keyUp.bind(this)}
                onFocus={this.onFocus.bind(this)}
            />
            {this.after()}
        </InputGroup>
    }
    label() {
        let { label, widthLabel } = this.props
        if (label !== undefined)
            return <span style={{ width: widthLabel, position: 'relative', backgroundColor: "#eee" }} className="form-control">{label}</span>
        return <></>
    }
    after() {
        let { after, widthAfter } = this.props
        if (after !== undefined)
            return <span style={{ width: widthAfter, position: 'relative', backgroundColor: "#eee" }} className="form-control">{after}</span>
        return <></>
    }
    stringValue() {
        let input = this.ref.current ? this.ref.current : false;
        if (input) {
            let { setData, update } = this.props;
            if (update !== undefined)
                setData({ [update]: input.value.toUpperCase() });
            input.value = input.value.toUpperCase();
        }
    }
    numberValue() {
        let input = this.ref.current;
        if (input) {
            let { minValue, setData, update } = this.props,
                r = (input.value === "") ? 0 : parseFloat(input.value);
            if (minValue)
                if (r < minValue)
                    r = minValue;
            if (update !== undefined)
                setData({ [update]: r });
        }
    }
    onBlur() {
        this.setState({ selected: false })
        let { type } = this.props
        if (type === "number") {
            this.numberValue();
        } else {
            this.stringValue();
        }
    }
    keyUp(e: React.KeyboardEvent<HTMLInputElement>) {
        let { update, setData, type, mask } = this.props
        let { value } = e.currentTarget
        if (type !== "number" && type !== 'password') {
            let r = value.toUpperCase()
            if (mask) {
                if (e.keyCode !== 8) {
                    r = maskFormat(r, mask);
                    e.currentTarget.value = r;
                }
                if (update !== undefined)
                    setData({ [update]: r })
            }
        }
    }
    onFocus() {
        this.setState({ selected: true })
        let input = this.ref.current;
        if (input) {
            let { value } = this.props;
            input.value = value + '';
        }
    }
    getValue() {
        let { value, type } = this.props;
        if (type === 'number') {
            let r = value as number;
            return this.state.selected ? r : numberFormat(r);
        }
        let s = value as string
        return s.toUpperCase();
    }
    getProps() {
        let { type, step } = this.props
        if (type === 'number') {
            if (this.state.selected) {
                return { step, minLength: 0, min: 0, type, defaultValue: this.getValue() };
            }
        }
        if (this.state.selected) {
            return { type, defaultValue: this.getValue() };
        }
        return { type, value: this.getValue() }
    }

}