import { sentDetalles, getSubtotal, condicionesNull, CondicionesData, GeneralData } from "./General";
var dataNull = {
    ...condicionesNull,
    cod_proveedor: '',

}
export interface Orden extends CondicionesData {
    cod_proveedor: string
}
export function sentOrden(e: Orden, iva: number) {
    let subtotal = getSubtotal(e.detalles),
        impuesto = subtotal * (iva / 100),
        total = subtotal + impuesto;
    let { cod_proveedor, validez, tiempo_entrega, forma_pago, nota } = e;
    return {
        cod_proveedor,
        validez,
        tiempo_entrega,
        forma_pago,
        nota,
        total,
        impuesto,
        subtotal,
        detalles: sentDetalles(e.detalles)
    }
}
export var ordenNull: Orden = dataNull;
export interface Compra extends GeneralData {
    cod_proveedor: string
    id_orden: number
    cod_documento: string
    cod_nota: string
    nun_control: string
    fecha_documento: string
}
export function sentCompra(e: Compra, iva: number) {
    let subtotal = getSubtotal(e.detalles),
        impuesto = subtotal * (iva / 100),
        total = subtotal + impuesto;
    let { cod_proveedor, nota, cod_documento, nun_control, fecha_documento, id_orden } = e;
    return {
        id_orden,
        cod_proveedor,
        cod_documento,
        nun_control,
        fecha_documento,
        nota,
        total,
        impuesto,
        subtotal,
        detalles: sentDetalles(e.detalles)
    }
}
export var compraNull: Compra = {
    ...dataNull,
    id_orden: 0,
    cod_documento: '',
    cod_nota: '',
    nun_control: '',
    fecha_documento: '',
}