import React from "react";
import { Button, ButtonGroup, Col, InputGroup, Panel, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { Cotizacion, sentCotizacion } from "../../Modelos/Factura";
import { condicionesNull, GeneralFila } from "../../Modelos/General";
import InputText from "../../Utils/InputText";
import SetText from "../../Utils/setTex";
import { getPreCache, post } from "../../Utils/Utiles";
import ClienteSelector from "../Clientes/ClienteSelector";
import BuscarCotizacion from "./BuscarCotizacion";
import BuscarGuardado from "./BuscarGuardado";
import AuxiliarCotizacion from "./Nuevo/AuxiliarCotizacion";
interface State extends Cotizacion {
    open: boolean
    cargar: boolean
    copiar: boolean
}
export default class NuevaCotizacion extends React.Component<any, State>{
    impuesto = 0;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...condicionesNull, cod_cliente: '', plantilla: 0, tasa: 0, dolar: false, copiar: false, open: false, cargar: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Cotizacion/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <BuscarCotizacion open={state.copiar} add={this.copiar.bind(this)} close={() => { this.setState({ copiar: false }) }} />
            <BuscarGuardado open={state.cargar} add={this.cargar.bind(this)} cargarCache={this.cargarCache.bind(this)} close={() => { this.setState({ cargar: false }) }} />
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVA COTIZACION</h4>
            </Panel.Heading>
            <Panel.Body>
                <ClienteSelector type='Cliente' Cliente={state.cod_cliente} add={this.setProveedor.bind(this)} />
                <Button style={{ marginBottom: '15px' }}
                    onClick={() => { this.setState({ open: !this.state.open }) }}
                    bsStyle='primary'>CONDICIONES</Button>
                <Col xs={4} className="pull-right">
                    <InputGroup style={{ width: '100%' }} className="mb-3">
                        <span style={{ width: '150px', position: 'relative', backgroundColor: "#eee" }} className="form-control">TASA ACTUAL</span>
                        <span style={{ width: 'calc(100% - 150PX)', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }} className="form-control">{state.tasa}</span>
                    </InputGroup>
                </Col>
                <Panel.Collapse>
                    <Panel bsStyle="info">
                        <Panel.Body>
                            <Row>
                                <Col xs={12}>
                                    <InputText maxLength={120} widthLabel={200} label="VALIDES DE LA OFERTA" value={state.validez} setData={this.setState} update="validez" />
                                </Col>
                                <Col xs={12}><br />
                                    <InputText maxLength={120} widthLabel={200} label="TIEMPO DE ENTREGA" value={state.tiempo_entrega} setData={this.setState} update="tiempo_entrega" />
                                </Col>
                                <Col xs={12}><br />
                                    <InputText maxLength={120} widthLabel={200} label="FORMA DE PAGO" value={state.forma_pago} setData={this.setState} update="forma_pago" />
                                </Col>
                                <Col xs={12}><br />
                                    <b>NOTA</b>
                                    <SetText type='textarea' rows={3} maxLength={250} value={state.nota} setData={this.setState} update="nota" />
                                </Col>
                            </Row>
                        </Panel.Body>
                    </Panel>
                </Panel.Collapse>
                <AuxiliarCotizacion Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} >
                    <Col style={{ width: "70px" }} xs={2}>
                        <Button onClick={() => { this.setState({ copiar: true }) }}
                            data-class="toolbody" data-html="true"
                            data-tip="COPIAR COTIZACION" bsStyle="primary">
                            <span className="glyphicon glyphicon-duplicate" />
                        </Button>
                    </Col>
                    <ButtonGroup>
                        <Button bsStyle="info" onClick={this.guardar.bind(this)} data-tip="GUARDAR">
                            <span className="glyphicon glyphicon-save-file"></span>
                        </Button>
                        <Button bsStyle="warning" onClick={() => { this.setState({ cargar: true }) }} data-tip="CARGAR">
                            <span className="glyphicon glyphicon-open-file" />
                        </Button>
                    </ButtonGroup>
                </AuxiliarCotizacion>
            </Panel.Body>
        </Panel>
    }
    copiar(d: GeneralFila) {
        getPreCache('api/Cotizacion/detalles/' + d.codigo, data => {
            data.codigo = 0;
            this.setState(data)
        })
    }
    cargar(d: GeneralFila) {
        getPreCache('api/Cotizacion/cargar/' + d.codigo, data => {
            data.plantilla = data.codigo;
            data.codigo = 0;
            this.setState(data)
        })
    }
    cargarCache() {
        let dataCache = localStorage.getItem('guardarNuevaCotizacion');
        if (dataCache !== null)
            this.setState(JSON.parse(dataCache));
    }
    guardar() {
        localStorage.setItem('guardarNuevaCotizacion', JSON.stringify(this.state));
        post('api/Cotizacion/guardar', sentCotizacion(this.state, this.impuesto), data => {
            this.setState({ plantilla: data });
            localStorage.setItem('guardarNuevaCotizacion', JSON.stringify(this.state));
            toast("DATOS GUARDADOS EN N¬∞ " + data, { type: toast.TYPE.SUCCESS })
        }, error => {
            toast("ERROR AL CONECTAR CON SEL SERVIDOR. GUARDADOS EN CACHE", { type: toast.TYPE.ERROR })
        });
    }
    componentDidMount() {
        getPreCache('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
        getPreCache('api/Dolares/valor', data => {
            this.setState({ tasa: data })
        })
    }
    setProveedor(p: string) {
        this.setState({ cod_cliente: p });
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (procesar)
            this.state.detalles.forEach(p => {
                if (p.unidades > p.cantidad) {
                    toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                    procesar = false;
                } else if (p.costo > p.precio) {
                    toast(p.descripcion + ' ESTA POR DEBAJO DE SU COSTO REGISTRADO', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            })
        if (procesar)
            post('api/Cotizacion/nuevo', sentCotizacion(this.state, this.impuesto), data => {
                this.setState({ codigo: data })
            })
    }
}