import React from "react";
import { Button, Col, Panel, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { Nota, sentNota } from "../../Modelos/Factura";
import { GeneralFila, generalNull } from "../../Modelos/General";
import CacheStorage from "../../Utils/CacheStorage";
import SetText from "../../Utils/setTex";
import { getPreCache, post } from "../../Utils/Utiles";
import ClienteSelector from "../Clientes/ClienteSelector";
import BuscarCotizacion from "../Cotizacion/BuscarCotizacion";
import AuxiliarFactura from "../Facturas/Nuevo/AuxiliarFactura";
interface State extends Nota {
    open: boolean
    cargar: boolean
}
export default class NuevaNota extends React.Component<any, State>{
    impuesto = 0;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...generalNull, cod_cliente: '', open: false, cargar: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Factura/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <BuscarCotizacion open={state.cargar} add={this.cargarCotizacion.bind(this)} close={() => { this.setState({ cargar: false }) }} />
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVA NOTA DE ENTREGA</h4>
            </Panel.Heading>
            <Panel.Body>
                <ClienteSelector type='Cliente' Cliente={state.cod_cliente} add={this.setCliente.bind(this)} />
                <Button style={{ marginBottom: '15px' }}
                    onClick={() => { this.setState({ open: !this.state.open }) }}
                    bsStyle='primary'>NOTA</Button>
                <Panel.Collapse>
                    <Panel bsStyle="info">
                        <Panel.Body>
                            <Row>
                                <Col xs={12}>
                                    <SetText type='textarea' rows={3} maxLength={600} value={state.nota} setData={this.setState} update="nota" />
                                </Col>
                            </Row>
                        </Panel.Body>
                    </Panel>
                </Panel.Collapse>
                <AuxiliarFactura Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} >
                    <Col style={{ width: "70px" }} xs={2}>
                        <Button onClick={() => { this.setState({ cargar: true }) }}
                            data-class="toolbody" data-html="true"
                            data-tip="COPIAR COTIZACION" bsStyle="primary">
                            <span className="fa fa-fw fa fa-calculator" />
                        </Button>
                    </Col>
                    <CacheStorage data={this.props.data} setData={this.props.setData} Identificador='NuevaNota' />
                </AuxiliarFactura>
            </Panel.Body>
        </Panel>
    }
    cargarCotizacion(d: GeneralFila) {
        getPreCache('api/Cotizacion/detalles/' + d.codigo, data => {
            data.id_cotizacion = data.codigo;
            data.codigo = 0;
            this.setState(data)
        })
    }
    componentDidMount() {
        getPreCache('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (procesar)
            this.state.detalles.forEach(p => {
                if (p.unidades > p.cantidad) {
                    toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                    procesar = false;
                } else if (p.costo > p.precio) {
                    toast(p.descripcion + ' ESTA POR DEBAJO DE SU COSTO REGISTRADO', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            })
        if (procesar)
            post('api/Factura/nuevo', sentNota(this.state, this.impuesto), data => {
                this.setState({ codigo: data })
            })
    }
}