import React from "react";
import { Button, ButtonGroup, Col, InputGroup, Panel, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { Cotizacion, Factura, sentFactura } from "../../Modelos/Factura";
import { GeneralFila, generalNull } from "../../Modelos/General";
import CacheStorage from "../../Utils/CacheStorage";
import SetText from "../../Utils/setTex";
import { getPreCache, post } from "../../Utils/Utiles";
import ClienteSelector from "../Clientes/ClienteSelector";
import BuscarCotizacion from "../Cotizacion/BuscarCotizacion";
import BuscarNota from "../Nota/BuscarNota";
import AuxiliarFactura from "./Nuevo/AuxiliarFactura";
interface State extends Factura {
    open: boolean
    cargarCotizacion: boolean
    cargarNota: boolean
}
export default class NuevaFactura extends React.Component<any, State>{
    impuesto = 0;
    tasa = 1;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...generalNull, cod_cliente: '', condicion: 'CONTADO', open: false, cargarCotizacion: false, cargarNota: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Nota/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <BuscarCotizacion open={state.cargarCotizacion} add={this.cargarCotizacion.bind(this)} close={() => { this.setState({ cargarCotizacion: false }) }} />
            <BuscarNota open={state.cargarNota} add={this.cargarNota.bind(this)} close={() => { this.setState({ cargarNota: false }) }} />
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVA FACTURA</h4>
            </Panel.Heading>
            <Panel.Body>
                <ClienteSelector type='Cliente' Cliente={state.cod_cliente} add={this.setCliente.bind(this)} />
                <Col xs={5}>
                    <InputGroup style={{ width: '100%' }} className="mb-3">
                        <span style={{ width: 100, position: 'relative', backgroundColor: "#eee" }}
                            className="form-control">CONDICION</span>
                        <select style={{ width: 'calc(100% - 100px)' }} onChange={this.setCondicion.bind(this)}
                            className="form-control"
                            defaultValue={this.props.condicion}>
                            <option value="CONTADO">CONTADO</option>
                            <option value="CREDITO">CREDITO</option>
                        </select>
                    </InputGroup>
                </Col>
                <Button style={{ marginBottom: '15px' }}
                    onClick={() => { this.setState({ open: !this.state.open }) }}
                    bsStyle='primary'>OBSERVACI√ìN</Button>
                <Panel.Collapse>
                    <Panel bsStyle="info">
                        <Panel.Body>
                            <Row>
                                <Col xs={12}>
                                    <SetText type='textarea' rows={4} maxLength={600} value={state.nota} setData={this.setState} update="nota" />
                                </Col>
                            </Row>
                        </Panel.Body>
                    </Panel>
                </Panel.Collapse>
                <AuxiliarFactura Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} >
                    <Col style={{ width: "118px" }} xs={2}>
                        <ButtonGroup>
                            <Button onClick={() => { this.setState({ cargarCotizacion: true }) }}
                                data-class="toolbody" data-html="true"
                                data-tip="CARGAR COTIZACION" bsStyle="primary">
                                <span className="fa fa-fw fa fa-calculator" />
                            </Button>
                            <Button onClick={() => { this.setState({ cargarNota: true }) }}
                                data-class="toolbody" data-html="true"
                                data-tip="CARGAR NOTA" bsStyle="primary">
                                <span className="glyphicon glyphicon-send" />
                            </Button>
                        </ButtonGroup>
                    </Col>
                    <CacheStorage setData={this.setState} data={state} Identificador="NuevaFactura" />
                </AuxiliarFactura>
            </Panel.Body>
        </Panel>
    }
    cargarCotizacion(d: GeneralFila) {
        getPreCache('api/Cotizacion/detalles/' + d.codigo, (data: Cotizacion) => {
            let d: any = data;
            d.id_cotizacion = data.codigo;
            d.codigo = 0;
            d.nota = this.state.nota;
            if (data.tasa !== 0)
                d.detalles = data.detalles.map(p => {
                    p.precio = p.precio * this.tasa / data.tasa;
                    p.precio = parseFloat(p.precio.toFixed(2));
                    return p;
                })
            this.setState(d)
        })
    }
    cargarNota(d: GeneralFila) {
        getPreCache('api/Nota/detalles/' + d.codigo, data => {
            data.id_nota = data.codigo;
            data.codigo = 0;
            data.nota = this.state.nota;
            this.setState(data)
        })
    }
    componentDidMount() {
        getPreCache('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
        getPreCache('api/Config/getFactura', data => {
            this.setState({ nota: data.observacion });
        })
        getPreCache('api/Dolares/valor', data => {
            this.tasa = data;
        })
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    setCondicion(v: React.ChangeEvent<HTMLSelectElement>) {
        let value = v.currentTarget.value as string
        this.props.change(value)
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (procesar)
            this.state.detalles.forEach(p => {
                if (p.unidades > p.cantidad) {
                    toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                    procesar = false;
                } else if (p.costo > p.precio) {
                    toast(p.descripcion + ' ESTA POR DEBAJO DE SU COSTO REGISTRADO', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            })
        if (procesar)
            post('api/Nota/nuevo', sentFactura(this.state, this.impuesto), data => {
                this.setState({ codigo: data })
            })

    }
}