import { Producto } from "./Productos";
export interface Detalles extends Producto {
    unidades: number,
    comentario: string,
    precio: number,
}
export interface GeneralFila {
    codigo: number
    fecha: string
    nombre: string
    telefono: string
    correo: string,
    contacto: string,
    nota: string
    tasa: number
    monto: number
    usuario: number
    status: 0 | 1 | 2
    detalles: string[]
}
export interface GeneralData {
    codigo: number,
    nota: string,
    fecha: string,
    subtotal: number,
    total: number,
    impuesto: number,
    detalles: Detalles[]
}
export interface CondicionesData extends GeneralData {
    forma_pago: string
    tiempo_entrega: string
    validez: string
}
export var generalNull: GeneralData = {
    codigo: 0,
    nota: '',
    fecha: '',
    subtotal: 0,
    total: 0,
    impuesto: 0,
    detalles: []
}
export var condicionesNull: CondicionesData = {
    ...generalNull,
    forma_pago: '',
    tiempo_entrega: '',
    validez: '',
}

export function getSubtotal(d: Detalles[]) {
    let subtotal = 0;
    d.forEach(e => {
        let { precio, unidades } = e;
        subtotal += precio * unidades;
    })
    return subtotal;
}
export function getCosto(d: Detalles[]) {
    let subtotal = 0;
    d.forEach(e => {
        let { costo, unidades } = e;
        subtotal += costo * unidades;
    })
    return subtotal;
}
export function sentDetalles(d: Detalles[]) {
    return d.map(e => {
        let { codigo, precio, unidades, comentario } = e;
        return { codigo, precio, unidades, comentario };
    })
}

export type tipoData = 'Cotizacion' | 'Orden' | 'Nota' | 'Factura' | 'Compra';